* {
  margin: 0;
}

.search-bar-nav {
  display: flex;
  flex-flow: column wrap;
  margin: 6px auto;
  width: 100%;
}

.input-text-search-bar {
  border: none;
  border-radius: 5px;
  margin: 0 auto;
  width: 95%;
}

button {
  background-color: transparent;
  border: none;
  margin: 0 auto;
  width: 95%;
}

.btn-search {
  background-color: orangered;
  border: none;
  border-radius: 6px;
  color: white;
  font-weight: 600;
  padding: 5px;
  width: 93%;
}

.search-radio-div {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  text-align: center;
  width: 95%;
}

.search-radio-div label {
  background-color: #704c13;
  border-radius: 3px;
  color: white;
  font-size: 0.8em;
  font-weight: 600;
  margin-right: 6px;
  padding: 3px;
  text-align: center;
  width: 30%;
}

.search-radio:hover {
  background-color: chartreuse;
  color: black;
}

[ type = 'radio' ] {
  display: none;
}
